.box-controllers {
    visibility: visible;
    background-color: white;
    display: flex;
    max-width: 24em;
    min-width: 21em;
    height: 35px;
    box-shadow: 0px 0px 20px 0px rgba(15, 15, 15, 0.2);
    margin-bottom: 30px;
    align-items: center;
    transition: visibility 0.5s ease-out;
}

.controllers {
    background-color: white;
    flex: 1 1;
    text-align: center;
    height: 100%;
    width: 100%;
    line-height: 2;
    cursor: pointer;
}

.card-fulls {
