/* CSS for Popup Review Form */
/* Button used to open the contact form - fixed at the bottom of the page */
.open-button {
    color: #0076cb;
    font-size: large;
    background-color: white;
    border: 2px solid #0076cb;
    border-radius: 5em;
    text-align: center;
    padding: 16px 20px;
    cursor: pointer;
    position: fixed;
    bottom: 23px;
    right: 28px;
    width: 250px;
  }

.open-button:hover {
    color: white;
    background-color: #0076cb;
}
  
  /* The popup form - hidden by default */
.form-popup {
    background-color: white;
    width: 300px;
    display: none;
    position: fixed;
    bottom: 0;
    right: 15px;
    border: 3px solid #f1f1f1;
    z-index: 9;
}
  
  /* Add styles to the form container */
.form-container {
    /* max-width: 300px; */
    text-align: center;
    height: 100%;
    padding: 10px;
    margin: auto;
}

.p_inline {
    color:  #cf4300;
    float: left;
    margin: 10px;
}
.reviewHeading{
    margin: 5px;
}

.reviewerIcon {
    max-height: 41px;
    max-width: 41px;
    float: left;
    padding: 0;
}

.reviewerName {
    width: 50%;
    float: left;
    padding: 0;
}

.reviewerRating {
    width: 50%;
    float: right;
    padding: 0;
}

.ratingFilter {
    width: 20%;
    float: right;
}

.dropbtn {
    background-color: #fafafa;
    color: #558000;
    font-size: large;
    box-sizing: border-box;
    border: 2px solid #558000;
    border-radius: 5em;
    padding: 8px;
    margin: 15px;  
    text-align: center; 
}

.dropbtn:hover {
    color: #fafafa;
    background-color: #6ca000;
}
  
.dropdown {
    position: relative;
    display: inline-block;
}
  
.dropdown-content {
    display: none;
    position: absolute;
    background-color: #f1f1f1;
    min-width: 200px;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 1;
}
  
.dropdown-content a {
    color: black;
    padding: 12px 16px;
    text-decoration: none;
    display: block;
}
  
.dropdown-content a:hover {background-color: #ddd;}
  
.dropdown:hover .dropdown-content {display: block;}
  
.dropdown:hover .dropbtn {background-color: #3e8e41; color: #fafafa}

.comments p{
    padding-left: 2%;
    padding-right: 2%;
    text-align: left;
}

.comment-container {
    display: none;
    text-align: center;
    height: 100%;
    padding: 10px;
    margin: auto;
}

#message {
    background-color: #fafafa;
    box-sizing: border-box;
    display: none;
    text-align: left;
    float: left; 
    width: 96%;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
    border-radius: 15px;
    padding: 1%;
    margin: 2%;
    display:none;
    /* position: relative; */
}

.btn:hover {
    cursor: pointer;
}
  
.valid {
    color: green;
    text-align: left;
    padding-left: 70px;
}
  
.valid:before {
    position: relative;
    text-align: left;
    left: -35px;
    content: "✔";
    padding-left: 70px;
}

.invalid {
    color: red;
    text-align: left;
    padding-left: 70px;
}
  
.invalid:before {
    position: relative;
    text-align: left;
    left: -35px;
    content: "✖";
    padding-left: 70px;
}

.profilePic{
    object-fit: cover;
    width: 200px;
    height: 200px;
    border-radius: 50%;
}
.profileIcon{
    object-fit: cover;
    height: 41px;
