}

@media screen and (max-width: 1500px) {
    .card{
        width: 30%;
    }
}

@media screen and (max-width: 900px) {
    .card,
    .card-wide{
        width: 98%;
    }

    #sticky{
        position: static;
        margin-top: 5%;
        float: left;
    }
    #right{
        float: left;
    }

    .ratingFilter {
        width: 40%;
        padding:0;
        margin: 0;
        float: right;
    }
}

